import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)


/* page */
const open = () => import('@/views/open')
const tousuLayout = () => import('@/views/tousu/layout')
const tousuList = () => import('@/views/tousu/list')
const tousuSubmit = () => import('@/views/tousu/submit')
const tousuRes = () => import('@/views/tousu/res')

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'open',
      component: open
    },  
    {
      path: '/tousu',
      component: tousuLayout,
      children: [
        {
          path: 'tousuList',
          name: 'tousuList',
          component: tousuList
        },
        {
          path: 'tousuSubmit',
          name: 'tousuSubmit',
          component: tousuSubmit
        },        
        {
          path: 'tousuRes',
          name: 'tousuRes',
          component: tousuRes
        }            
      ]
    }
  ]
})



